<template>
  <div class="pay">
    <van-nav-bar
      title="提交订单"
      left-text="返回"
      left-arrow
      fixed
      @click-left="goback"
    />

    <div class="addr">
      <div class="addr_box">
        <div class="select-add">
          <span @click="showSelectAddressList">选择收货地址</span>
          <van-icon name="arrow" />
        </div>
        <div class="userInfo">
          <div class="name">{{this.currentAddress.name}}</div>
          <div class="phone">{{this.currentAddress.tel}}</div>
            <div class="defaultAddress" v-if="currentAddress.isDefault">默认</div>
        </div>
        <div class="addrInfo">{{this.currentAddress.address}}</div>
      </div>

     
       <OrderList
        :leftInfo="leftInfo"
        :isShow="false"
        :count="productInfo.count"
        :total="productInfo.total"
        
       >
         <OrderItem v-for="(item,index) in products"
          :key="index" :item="item"></OrderItem>
        
       </OrderList>       
          <div class="btn_submit">
             <van-button type="primary" block round color="#7c1400" @click="payToast">立即结算</van-button>
          </div>
      </div>
       <div>
         <van-popup v-model="isOpen" position="bottom" closeable> 
              <div class="select">
                选择收货地址
              </div>
           <van-address-list
             v-model="chosenAddressId"
             :list="list"
             default-tag-text="默认"
            @add="add"
            @select="selectAddress"
           />
         </van-popup>

         </div>    
  </div>
</template>

<script>
import "../assets/less/pay.less";
import OrderItem from '../components/OrderItem.vue';
import OrderList from '../components/OrderList.vue';
export default {
  components: { OrderList, OrderItem },
  name: "Pay",
  created() {
    
    // 截取查询参数
    this.sids = this.$route.query.sids.split('-');

    // 根据sids获取购物袋数据
    this.getShopbagBySids();

     this.findAddress()
  },

  data(){
    return{    
    // 购物袋的sid
    sids:[],
    isOpen:'false',
    // 需要购买的商品数据
    products:[],
    //商品数量和订单总价
    productInfo: {
     count: 0,
     total: 0
      },
      isOpen:false,
      chosenAddressId:1,
      list:[],
      // 选择的收货地址
      currentAddress:{},
      leftInfo:'订单信息'
    }
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    // 根据sids获取购物袋的数据
    getShopbagBySids(){

     let tokenString = localStorage.getItem("tokenString");

     if(!tokenString){
       this.$toast("请先登录账户")
       this.$router.push({name:'Login'})
     }

     this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({

        method:'GET',
        url:'/commitShopcart',
        params:{
          appkey:this.appkey,
          tokenString,
          sids:JSON.stringify(this.sids)
        }

      }).then(result =>{
        this.$toast.clear();
        if (result.data.code == 700) {
            this.$router.push({ name: "Login" });
          } else if (result.data.code == 50000) {
            if (result.data.result.length == 0) {
              return this.$router.push({name: 'Home'});
            }
            result.data.result.map(v => {
              this.productInfo.count += v.count;
              this.productInfo.total += v.count * v.price;
            })
            this.products = result.data.result;
          } else {
            this.$toast(result.data.msg);
          }
       
      }).catch(err =>{
        console.log(err)
      })
    },
    // 弹出选择地址框
    showSelectAddressList(){
      this.isOpen = true;
    },
    // 查询填写的地址
     findAddress(){

        let tokenString = localStorage.getItem('tokenString');

        if(!tokenString){
            this.$toast("请先登录账户");
            return this.$toast.push({name:'Login'});

        }

       this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration: 0
        });

        this.axios({
            method:'GET',
            url:'/findAddress',
            params:{
                appkey:this.appkey,
                tokenString
            }
        }).then(result =>{
            this.$toast.clear()
          if(result.data.code == 700){
              this.$toast(result.data.msg)
          }else if(result.data.code == 20000){
             
             result.data.result.map(v =>{
                 v.isDefault = Number(v.isDefault);
                 v.id = v.aid;
                 v.address = `${v.province}${v.city}${v.county}${v.addressDetail}`;
                 //选择默认地址
              if (v.isDefault) {
                this.chosenAddressId = v.aid;
                this.currentAddress = v;
              }
             })

              this.list = result.data.result;
          }

        }).catch(err =>{
            this.$toast.clear()
            console.log(err)
        })
    },
    // 新增地址
    add(){
      this.$router.push({name:'NewAddress'})
    },

    // 选择地址
    selectAddress(item,index){
      // console.log(item)
      // console.log(index)
      this.isOpen = false;
      this.currentAddress = item;

    },
    // 立即结算
    pay(){
      let tokenString = localStorage.getItem("tokenString");

      if(!tokenString){
        this.$toast("请先登录");
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method:'POST',
        url:'/pay',
        data:{
          appkey:this.appkey,
          tokenString,
          sids:JSON.stringify(this.sids),
          phone:this.currentAddress.tel,
          address:this.currentAddress.address,
          receiver:this.currentAddress.name

        }
      }).then(result =>{
        this.$toast.clear()
        if(result.data.code == 700){
          this.$router.push({name:'Login'})
        }else{

          if(this.currentAddress == ''){
            this.$toast("请选择地址");
            return ;
          }

          this.$toast(result.data.msg);
          this.$router.push({name:'Order'})
        }
      }).catch(err =>{
        this.$toast.clear()
        console.log(err);
      })
    },

    // 确认结算框
    payToast(){
       this.$dialog.confirm({
        title: '确认订单',
        message: '是否立即结算?',
        confirmButtonColor: '#780d00'
      }).then(() => {

        this.pay();

      }).catch(err => {
        console.log('err ==> ', err);
      })
    }
  },
};
</script>

